.advancedSearch{
    width: 382px;
    height: 1180px;
    left: 0px;
    top: 200px;
    }
    
    
    .advancedSearchMain{
        margin: 0 auto;
        width: 256px;
        height: 100%;
    
    
    }
    
    .advancedSearchHeader{
        display: flex;
        align-items: center;
        width: 256px;
        padding-top: 54px ;
        margin-bottom: 40px; 
        font-family: Open Sans;
        font-weight: 600;
        font-size: 24px;
        color: #2D4C68;
        opacity: 0.6;
    }
    
    
    .advancedSearchTitle{
        font-family: Open Sans;
        font-style: normal;
        font-weight: normal;
        font-size: 18px;
        line-height: 40px;
        display: flex;
        align-items: center;
        color:  #2d4c68;
        flex: none;
        order: 0;
        flex-grow: 0;
        margin: 0px 0px;
    }
    
    .advancedSearchSpan{
    font-family: Open Sans;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 22px;
    color: #595959;
    align-self: center;
    flex-grow: 0;
    }
    
    
    .advancedSearchSpanYears{
        font-family: Open Sans;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 22px;
        color: #595959;
        align-self: center;
        flex-grow: 0;
        padding-left: 16px;
        }
    
    .selectStyle{
        width: 256px;
        border: 1px solid #D9D9D9;
        box-sizing: border-box;
        border-radius: 6px;
    }
    
    .imageNoData{
        margin: 0 auto;
       display: flex;
       cursor: default;
    }
    
    .inputChildYears{
    display: flex;
        width: 30px;
        height: 40px;
    }